// Importando o componenet UserProfile

const Privacy = () => (
<div>
      <div id='privacy' className='secondary-header'>
        <div className='container'>
          <div className='col-md-12'>
            <div className='row'>
              <div className='section-title'>
                <h2>Política de Privacidade</h2>
<ol>
<li><strong> SEUS DADOS S&Atilde;O SEUS</strong></li>
</ol>
<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Esta Pol&iacute;tica de Privacidade ir&aacute; ajud&aacute;-lo a compreender quais s&atilde;o os dados pessoais coletados, porque os coletamos e aquilo que fazemos com eles. Por favor dedique alguns instantes a conhecer as nossas pr&aacute;ticas de privacidade e, caso tenha quaisquer quest&otilde;es, contate-nos atrav&eacute;s do e-mail <a href="mailto:contato@canimandra.com.br">contato@canimandra.com.br</a>. Esta Pol&iacute;tica poder&aacute; ser atualizada a qualquer tempo, pela Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o, mediante aviso no site e/ou por e-mail, se o Usu&aacute;rio tiver optado por receber nossas comunica&ccedil;&otilde;es.</p>
<p>Voc&ecirc; tem o direito de se opor ao tratamento dos seus dados pessoais, incluindo a utiliza&ccedil;&atilde;o dos seus dados pessoais para marketing direto, quando n&atilde;o se incluir numa hip&oacute;tese de cumprimento de obriga&ccedil;&atilde;o legal ou regulat&oacute;ria, execu&ccedil;&atilde;o de um contrato ou ainda quando para fins de tutela da sa&uacute;de. Veja aqui quais s&atilde;o os seus direitos e como os pode exercer.</p>
<p>&nbsp;</p>
<ol start="2">
<li><strong> QUEM OS COLETA e COM QUEM OS DADOS S&Atilde;O COMPARTILHADOS?</strong></li>
</ol>
<p>A Canimandra coleta alguns dados pessoais no momento do cadastro no Curso. N&atilde;o enviamos conte&uacute;do de marketing, portanto, seus dados pessoais n&atilde;o ser&atilde;o utilizados para este fim. As informa&ccedil;&otilde;es coletadas pela Canimandra n&atilde;o s&atilde;o compartilhadas com outras pessoas, empresas e/ou institui&ccedil;&otilde;es.</p>
<p>O Usu&aacute;rio poder&aacute; fazer solicita&ccedil;&otilde;es relativas a seus dados pessoais tratados pela Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o encaminhando um e-mail para contato@canimandra.com.br. Tentamos responder a todas as solicita&ccedil;&otilde;es leg&iacute;timas dentro de um prazo de 15 dias &uacute;teis. Eventualmente, um prazo maior poder&aacute; ser necess&aacute;rio, caso a(s) solicita&ccedil;&atilde;o(&otilde;es) sejam complexas e/ou numerosas. Neste caso, iremos inform&aacute;-lo e mant&ecirc;-lo atualizado acerca do andamento de sua(s) solicita&ccedil;&atilde;o(&otilde;es).</p>
<p>&nbsp;</p>
<ol start="3">
<li><strong> QUAIS DADOS PESSOAIS S&Atilde;O COLETADOS?</strong></li>
</ol>
<p>Os dados pessoais que coletamos s&atilde;o de uso exclusivo para personaliza&ccedil;&atilde;o, inscri&ccedil;&atilde;o/participa&ccedil;&atilde;o no Treinamento e consistem em:</p>
<p>- Nome social;</p>
<p>- CPF;</p>
<p>- Telefone/whatsapp;</p>
<p>- E-mail;</p>
<p>- Escolaridade;</p>
<p>-Profiss&atilde;o;</p>
<p>- G&ecirc;nero.</p>
<p>&nbsp;</p>
<p>Alguns outros dados pessoais poder&atilde;o ser solicitados outros dados pessoais exclusivamente para a emiss&atilde;o do Certificado de Conclus&atilde;o do Treinamento.</p>
<p>Excetuando-se os dados pessoais tratados em decorr&ecirc;ncia de cumprimento de obriga&ccedil;&atilde;o legal, para execu&ccedil;&atilde;o de um contrato ou, ainda, em raz&atilde;o da tutela da sa&uacute;de, voc&ecirc; n&atilde;o &eacute; obrigado a nos fornecer dados pessoais que solicitamos, mas caso escolha n&atilde;o o fazer, poderemos n&atilde;o conseguir oferecer-lhe os nossos produtos e servi&ccedil;os, ou um servi&ccedil;o de alta qualidade ou responder a alguma quest&atilde;o que possa ter.</p>
<p>&nbsp;</p>
<ol start="4">
<li><strong> MODO QUE UTILIZAMOS PARA COLETAR OS SEUS DADOS PESSOAIS</strong></li>
</ol>
<p>Somente coletamos dados pessoais que voc&ecirc; nos fornece diretamente; n&atilde;o coletamos cookies de sess&atilde;o ou outros cookies que possam identific&aacute;-lo.</p>
<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Quando apropriado, ser&aacute; solicitado o Consentimento do Usu&aacute;rio para o tratamento dos seus dados pessoais. Onde tiver fornecido o seu consentimento para as atividades de tratamento, ter&aacute; o direito de retirar o seu consentimento em qualquer momento.</p>
<p>&nbsp;</p>
<ol start="5">
<li><strong> QUANDO E COMO COLETAMOS &ldquo;CATEGORIAS ESPECIAIS DE DADOS PESSOAIS&rdquo;?</strong></li>
</ol>
<p>Certas categorias de dados pessoais s&atilde;o classificadas como &ldquo;categorias especiais de dados pessoais&rdquo; ou &ldquo;dados sens&iacute;veis&rdquo; e t&ecirc;m prote&ccedil;&atilde;o adicional. Como este Treinamento &eacute; destinado exclusivamente a mulheres trans, &eacute; poss&iacute;vel que haja a coleta de dados sens&iacute;veis (g&ecirc;nero), que ser&atilde;o utilizados unicamente para a realiza&ccedil;&atilde;o da inscri&ccedil;&atilde;o, sendo exclu&iacute;dos em seguida e totalmente eliminados de nossas bases de dados.</p>
<p>&nbsp;</p>
<ol start="6">
<li><strong> PARA QUE FINALIDADE UTILIZAMOS OS SEUS DADOS?</strong></li>
</ol>
<p>N&oacute;s coletamos e tratamos os seus dados pessoais apenas para finalidades espec&iacute;ficas e limitadas. Por exemplo, para cumprimento de atividades de um contrato, para cumprimento de uma obriga&ccedil;&atilde;o legal, para avaliar e lidar com quaisquer reclama&ccedil;&otilde;es, para desenvolver e melhorar os nossos produtos, servi&ccedil;os, m&eacute;todos de comunica&ccedil;&atilde;o e a funcionalidade. N&atilde;o tratamos seus dados por meios automatizados.</p>
<p>&nbsp;</p>
<ol start="7">
<li><strong> TRANSFER&Ecirc;NCIA INTERNACIONAIS DE DADOS</strong></li>
</ol>
<p>A Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o n&atilde;o transfere diretamente dados pessoais coletados no Brasil para outros pa&iacute;ses, no entanto, nosso servidor encontra-se na Alemanha, que obedece &agrave; GDPR (General Data Protection Regulation &ndash; Regulamento Geral de Prote&ccedil;&atilde;o de Dados). Cabe ressaltar que a Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o tem sua sede no Brasil e os dados que coleta s&atilde;o regidos pela Lei brasileira 13.709 (LGPD). Ao acessar ou usar nossos servi&ccedil;os, o Usu&aacute;rio entende e concorda com o processamento tais dados.</p>
<p>&nbsp;</p>
<ol start="8">
<li><strong> COMO PROTEGEMOS OS SEUS DADOS PESSOAIS?</strong></li>
</ol>
<p>Levamos muito a s&eacute;rio a seguran&ccedil;a dos seus dados pessoais. Investimos todos os esfor&ccedil;os para proteger os seus dados pessoais de uso indevido, interfer&ecirc;ncia, perda, acesso n&atilde;o autorizado, modifica&ccedil;&atilde;o ou divulga&ccedil;&atilde;o. As nossas medidas incluem a implementa&ccedil;&atilde;o de controles de acesso apropriados, investindo na mais recente capacidade de seguran&ccedil;a da informa&ccedil;&atilde;o para proteger os nossos ambientes informatizados, assegurando a criptografia bem como a anonimiza&ccedil;&atilde;o dos dados pessoais sempre que poss&iacute;vel.</p>
<p>Nosso banco de dados &eacute; criptografado e o acesso aos seus dados pessoais &eacute; permitido apenas entre os nossos funcion&aacute;rios em fun&ccedil;&atilde;o da necessidade de conhecimento e est&atilde;o sujeitos a rigorosas obriga&ccedil;&otilde;es contratuais de confidencialidade.</p>
<p>&nbsp;</p>
<ol start="9">
<li><strong> COMO ARMAZENAMOS OS SEUS DADOS PESSOAIS?</strong></li>
</ol>
<p>Seus dados s&atilde;o mantidos em um banco de dados criptografado, ao qual t&ecirc;m acesso somente agentes da Canimandra Tecnologia da Informa&ccedil;&atilde;o, sendo proibido expressamente o acesso por terceiros. Ao t&eacute;rmino da finalidade &agrave; qual os dados s&atilde;o destinados, eles ser&atilde;o eliminados permanentemente do nosso banco de dados, ou anonimizados, exceto em casos de cumprimento de obriga&ccedil;&atilde;o legal, fins judiciais ou exig&ecirc;ncia por autoridade competente.</p>
<p>&nbsp;</p>
<ol start="10">
<li><strong> QUAIS S&Atilde;O OS SEUS DIREITOS?</strong></li>
</ol>
<p>Voc&ecirc; &eacute; titular de um conjunto de direitos em rela&ccedil;&atilde;o aos seus dados pessoais e ao modo como s&atilde;o tratados. Poder&aacute; exercer esses direitos em qualquer momento.</p>
<p>Fornecemos abaixo um resumo geral desses direitos, juntamente com aquilo que implicam para si. Pode exercer os seus direitos enviando-nos um e-mail <a href="mailto:contato@canimandra.com.br">contato@canimandra.com.br</a>.</p>
<p>&nbsp;</p>
<p><strong>O direito &agrave; informa&ccedil;&atilde;o: </strong></p>
<ul>
<li>O titular dos dados tem direito a que lhe seja concedida informa&ccedil;&atilde;o concisa, transparente, compreens&iacute;vel e de f&aacute;cil acesso, utilizando uma linguagem clara e simples, sobre o modo como utilizamos os seus dados pessoais e sobre os seus direitos.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito de acesso e retifica&ccedil;&atilde;o:</strong></p>
<ul>
<li>O titular dos dados tem direito a aceder aos seus dados pessoais, bem como a retificar os dados pessoais inexatos que lhe digam respeito, ou a complet&aacute;-los caso se mostrem incompletos. Compreendemos a import&acirc;ncia destes direitos e caso os deseje exercer, por favor contate-nos.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito &agrave; portabilidade dos dados:</strong></p>
<ul>
<li>Os dados pessoais que nos facilitou podem ser port&aacute;veis. Isto significa que poder&atilde;o ser transferidos, copiados ou transmitidos eletronicamente em certas circunst&acirc;ncias.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito ao apagamento dos dados:</strong></p>
<ul>
<li>Em determinadas condi&ccedil;&otilde;es, o titular dos dados tem o direito de obter do respons&aacute;vel pelo tratamento o apagamento dos seus dados pessoais, sem demora justificada. Caso deseje apagar os seus dados pessoais, por favor informe-nos e tomaremos as medidas razo&aacute;veis para responder ao seu pedido de acordo com os requisitos legais. Caso os dados pessoais que coletamos deixem de ser necess&aacute;rios para qualquer fim e n&atilde;o formos por contrato ou por lei obrigados a mant&ecirc;-los, eles ser&atilde;o eliminados ou anonimizados definitivamente.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito &agrave; limita&ccedil;&atilde;o do tratamento:</strong></p>
<ul>
<li>Em determinadas condi&ccedil;&otilde;es, o titular dos dados tem o direito de limitar o tratamento dos seus dados pessoais.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito de oposi&ccedil;&atilde;o:</strong></p>
<ul>
<li>O titular dos dados tem o direito de se opor a qualquer momento, por motivos relacionados com a sua situa&ccedil;&atilde;o particular, ao tratamento dos dados pessoais que lhe digam respeito, incluindo a defini&ccedil;&atilde;o de perfis nos termos da Lei Geral de Prote&ccedil;&atilde;o de Dados. Nomeadamente, quando os dados pessoais forem tratados para efeitos de comercializa&ccedil;&atilde;o direta, o titular dos dados tem o direito de se opor a qualquer momento ao tratamento dos dados pessoais que lhe digam respeito para os efeitos da referida comercializa&ccedil;&atilde;o (ex. receber e-mails de notifica&ccedil;&atilde;o nossos ou ser contatado com potenciais oportunidades variadas).</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito a apresentar uma queixa &agrave; Autoridade Nacional de Prote&ccedil;&atilde;o de Dados:</strong></p>
<ul>
<li>Tem o direito de apresentar reclama&ccedil;&atilde;o &agrave; Autoridade Nacional de Prote&ccedil;&atilde;o de Dados sobre o modo como tratamos os seus dados pessoais.</li>
</ul>
<p>&nbsp;</p>
<p><strong>O direito a retirar o consentimento: </strong></p>
<ul>
<li>Caso tenha dado o seu consentimento &agrave; nossa utiliza&ccedil;&atilde;o dos seus dados pessoais, o titular tem o direito de retirar o seu consentimento em qualquer momento (a retirada do consentimento n&atilde;o compromete a licitude do tratamento efetuado com base no consentimento previamente dado), comunicando-nos a sua inten&ccedil;&atilde;o atrav&eacute;s dos contatos fornecidos acima.</li>
</ul>
<p>&nbsp;</p>
<p><strong>Direitos relacionados com a tomada de decis&otilde;es automatizadas:</strong></p>
<ul>
<li>O titular dos dados tem o direito de n&atilde;o ser sujeito a decis&otilde;es unicamente baseadas em processos automatizados que produzam efeitos legais ou outros efeitos significativos.</li>
</ul>
<p>&nbsp;</p>
<ol start="11">
<li><strong> POR QUANTO TEMPO ARMAZENAMOS OS SEUS DADOS?</strong></li>
</ol>
<p>A Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o manter&aacute; os dados pessoais dos Usu&aacute;rios somente pelo tempo que for necess&aacute;rio para cumprir com as finalidades para as quais os coletamos, neste caso, especificamente, enquanto estiver se inscrevendo e/ou realizando nosso treinamento, manteremos os dados relativos &agrave; sua ades&atilde;o, para que possamos cumprir o contrato espec&iacute;fico que celebrou ou ainda uma lei ou norma regulamentadora espec&iacute;fica que exija o seu armazenamento. &nbsp;</p>
<p>Guardamos os dados identific&aacute;veis durante o m&iacute;nimo tempo poss&iacute;vel, ap&oacute;s o qual tomaremos medidas para os apagar definitivamente, de modo seguro, ou em alguns casos anonimizaremos, quando cessar a obriga&ccedil;&atilde;o legal, comercial ou do consumidor para que sejam mantidos, salvo se houver qualquer outra raz&atilde;o para a sua manuten&ccedil;&atilde;o, como eventual obriga&ccedil;&atilde;o legal de reten&ccedil;&atilde;o de dados, ou necessidade de preserva&ccedil;&atilde;o destes para resguardo de direitos da Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o. Seus dados sens&iacute;veis s&atilde;o eliminados imediatamente ap&oacute;s a confirma&ccedil;&atilde;o da sua identidade no cadastro.</p>
<p>Para determinar o per&iacute;odo de reten&ccedil;&atilde;o adequado para os dados pessoais, consideramos a quantidade, a natureza e a sensibilidade dos dados pessoais, o risco potencial de danos decorrentes do uso n&atilde;o autorizado ou da divulga&ccedil;&atilde;o de seus dados pessoais, a finalidade de processamento dos seus dados pessoais e se podemos alcan&ccedil;ar tais prop&oacute;sitos atrav&eacute;s de outros meios, bem como os requisitos legais aplic&aacute;veis.</p>
<p>&nbsp;</p>
<p>&nbsp;</p>
<ol start="12">
<li><strong> COMO ACIONAR A CANIMANDRA TECNOLOGIA E SEGURAN&Ccedil;A DA INFORMA&Ccedil;&Atilde;O?</strong></li>
</ol>
<p>Como Encarregado da Prote&ccedil;&atilde;o de Dados (DPO), fica nomeada Lisiane Freitas, inscrita no CPF sob o n&deg; 005.279.349-41, com sede na rua 25 de novembro, 16, Nossa Senhora do Ros&aacute;rio, S&atilde;o Jos&eacute; &ndash; SC, CEP 88110-690, e poder&aacute; ser contatada atrav&eacute;s do e-mail contato@canimandra.com.br.</p>
<p>&nbsp;</p>
<ol start="13">
<li><strong> ATUALIZA&Ccedil;&Otilde;ES DESTA POL&Iacute;TICA DE PRIVACIDADE</strong></li>
</ol>
<p>Esta Pol&iacute;tica poder&aacute; ser atualizada a qualquer tempo pela Canimandra Tecnologia e Seguran&ccedil;a da Informa&ccedil;&atilde;o. Por isso, recomendamos que o Usu&aacute;rio acesse nossa Pol&iacute;tica periodicamente, para que tenha conhecimento sobre eventuais modifica&ccedil;&otilde;es. Ao final desta Pol&iacute;tica, h&aacute; o indicativo da data da &uacute;ltima atualiza&ccedil;&atilde;o e, caso sejam feitas altera&ccedil;&otilde;es relevantes que ensejem novas autoriza&ccedil;&otilde;es dos Usu&aacute;rios, ser&aacute; publicada tal atualiza&ccedil;&atilde;o e possivelmente requerido um novo consentimento.</p>
<p>&nbsp;</p>
<ol start="14">
<li><strong> LEGISLA&Ccedil;&Atilde;O E FORO</strong></li>
</ol>
<p>Esta Pol&iacute;tica ser&aacute; regida, interpretada e executada de acordo com as Leis da Rep&uacute;blica Federativa do Brasil, especialmente a Lei Federal n&ordm; 13.709/2018, independentemente das Leis de outros estados ou Pa&iacute;ses, sendo competente o foro da Comarca de S&atilde;o Jos&eacute; - SC para dirimir qualquer d&uacute;vida decorrente deste documento.</p>
<p>&nbsp;</p>
<p><strong>Atualizado em 25/09/2021.</strong></p>
              </div>
            </div>
          </div>
          </div>
          </div>
          </div>
);

export default Privacy;